.border {
  border: 1px solid #d0d7de;
  border-radius: 6px;
}

.input {
  width: 100%;
  background: #f6f8fa;
  padding: 5px 12px;
  line-height: 20px;
  box-shadow: inset 0 1px 0 rgba(208, 215, 222, 0.2);
}

.textarea {
  height: 200px;
  min-height: 200px;
  margin-top: 8px;
  resize: none;
}

.error {
  outline-color: red;
}